import useSWR, { SWRResponse } from 'swr';

function timeout(ms: number) {
  return new Promise((resolve) => setTimeout(resolve, ms));
}

type Lyrics = string;

interface SuspenseResponseInterface extends SWRResponse<Lyrics, any> {
  data: Lyrics;
}

export const useLyrics = () => {
  return useSWR(
    'lyrics',
    async () => {
      await timeout(5000);
      return `燈會破滅 心可撕裂 愛難以肢解
冰會酷熱 糖可苦澀 懶跟它變態

對現實 怎麼踩
都不會 扭轉世界
明白血肉會崩壞 骨氣沒法被活埋
來日想打妖怪 先要做人去
首先祝你心理愉快

抬頭尚有天空 敲不碎
埋頭尚有智慧 思想 他人難偷取
軟弱無力全是 堅忍的證據
靈魂內有信仰 搶不去
這種搶匪也許 比你畏懼
想保無邪之軀
還是必須好好過下去

可以戰敗 不可收買 信人會失敗
從來未信 犧牲尊嚴 竟可跳出魔界

鬥命長 多出街 怪物來 不必見怪
無論鐵路會崩壞 方向未會被活埋
殘夢歸於烏有 還有這心態
信最後善良留在世界

抬頭尚有天空 敲不碎
埋頭尚有智慧 思想 他人難偷取
痛恨無能為力 也沒有不對
只需要 死不去 流淚都必須喝水
信未來 死不去 仍舊有你我戰鬥的生趣

抬頭尚有天空 敲不碎
埋頭尚有智慧 思想 他人難偷取
軟弱無力全是 堅忍的證據
靈魂內有信仰 搶不去
這種搶匪也許 比你畏懼
想保無邪之軀
還是必須好好過下去`;
    },
    { suspense: true }
  ) as SuspenseResponseInterface;
};
